<template>
  <div>
    <el-card class="box-card" style="margin: 10px 10px">
      <div slot="header" class="clearfix" style="text-align:left">
        <span style="font-size:20px">乳山市健康查体项目指示者家庭成员登记表</span>
      </div>
      <el-form ref="form" :model="form" style="text-align:left;margin-top:10px" label-width="278px" :rules="rules" v-loading="loading">
        <el-form-item label="地址" style="color:darkgrey">
          乳山市<el-form-item prop="address1" style="display: inline-block;">
            <el-select v-model="form.address1" placeholder="请选择" style="width:162px" @change="addrChange">
              <el-option v-for="item in address1Options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          乡镇<el-form-item prop="address2" style="display: inline-block;">
            <el-input style="width:163px" v-model="form.address2" @change="addrChange"></el-input>
          </el-form-item>村（居委会）
        </el-form-item>
        <el-form-item label="填表人" prop="inputUser">
          <el-input style="width:400px" v-model="form.inputUser"></el-input>
        </el-form-item>
        <el-form-item label="填表日期" prop="inputDate">
          <el-date-picker v-model="form.inputDate" style="width:400px" align="right" format="yyyy-MM-dd" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="指示者编号" prop="pointManNo" style="color:darkgrey">
          <el-input style="width:400px" v-model.number="form.pointManNo"></el-input>
          （4位，直接从打印名单表中誊抄）
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input style="width:400px" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-radio-group v-model="form.sex">
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="目前状况" prop="healthy">
          <el-radio-group v-model="form.healthy">
            <el-radio label="健在"></el-radio>
            <el-radio label="去世"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否从本村迁出">
          <el-radio-group v-model="form.inCurrentCountry">
            <el-radio label="是"></el-radio>
            <el-radio label="否"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="出生日期" prop="birthDate">
          <el-date-picker v-model="form.birthDate" style="width:400px" align="right" value-format="yyyy-MM-dd" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="现住址" prop="currentAddr">
          <el-input style="width:400px" v-model="form.currentAddr"></el-input>
        </el-form-item>
        <el-form-item label="身份证号" prop="idCardNo">
          <el-input style="width:400px" v-model="form.idCardNo"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobilePhone">
          <el-input style="width:400px" v-model="form.mobilePhone"></el-input>
        </el-form-item>
        <el-form-item label="该指示者与本村其他指示者是否有亲缘关联">
          <el-radio-group v-model="form.isOtherPointmanRel">
            <el-radio label="是"></el-radio>
            <el-radio label="否"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="关联人" v-if="form.isOtherPointmanRel == '是'" prop="otherPointmanName">
          <el-input style="width:400px" v-model="form.otherPointmanName"></el-input>
        </el-form-item>
        <el-form-item label="与关联人关系" v-if="form.isOtherPointmanRel == '是'" prop="otherPointmanRel">
          <el-select style="width:400px;" v-model="form.otherPointmanRel" placeholder="请选择">
            <el-option v-for="item in relations" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
          <el-input v-if="form.otherPointmanRel == '09'" style="width:200px" v-model="form.otherPointmanRelOther"></el-input><span v-if="form.otherPointmanRel == '09'">2012年后共同居住1年以上者，请注明关系：（比如：公婆， 妯娌等）</span>
        </el-form-item>
        <el-form-item label="目前本村居住">
          <el-button size="small" type="success" @click="addIn">添加</el-button>
          <el-table :data="inKinShips" ref="inKinShipsTable" stripe style="width: 100%">
            <el-table-column prop="kinshipCode" label="亲缘关系" width="180">
              <template slot-scope="scope">
                <el-select :style="scope.row.kinshipCode == '09'?'width:45%':'width:100%'" @input="kinshipCodeValidate(scope.row)" v-model="scope.row.kinshipCode" placeholder="请选择">
                  <el-option v-for="item in relations" :title="item.label" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <el-input v-if="scope.row.kinshipCode == '09'" @input="kinshipCodeValidate(scope.row)" :style="scope.row.kinshipCode == '09'?'width:52%':''" v-model="scope.row.kinshipCodeOther"></el-input>
                <el-tooltip v-if="scope.row.kinshipCodeTip" :content="scope.row.kinshipCodeTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="100">
              <template slot-scope="scope">
                <el-input v-model="scope.row.name" style="width:100%;" @input="nameValidate(scope.row)"></el-input>
                <el-tooltip v-if="scope.row.nameTip" :content="scope.row.nameTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="sex" label="性别" width="100">
              <template slot-scope="scope">
                <el-select v-model="scope.row.sex" style="width:100%;">
                  <el-option value="男" label="男"></el-option>
                  <el-option value="女" label="女"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="birthdate" label="出生日期">
              <template slot-scope="scope">
                <el-date-picker style="width:100%;" @input="birthdateValidate(scope.row)" v-model="scope.row.birthdate" value-format="yyyy-MM-dd" align="right" type="date" placeholder="选择日期">
                </el-date-picker>
                <el-tooltip v-if="scope.row.birthdateTip" :content="scope.row.birthdateTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="idcardNo" label="身份证号">
              <template slot-scope="scope">
                <el-input v-model="scope.row.idcardNo" style="width:100%;" @input="idcardNoTableValidate(scope.row)"></el-input>
                <el-tooltip v-if="scope.row.idcardNoTip" :content="scope.row.idcardNoTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="remark" label="备注">
              <template slot-scope="scope">
                <el-input type="textarea" style="width:100%;" v-model="scope.row.remark"></el-input>
              </template>
            </el-table-column>
            <el-table-column prop="remark" label="操作" width="50">
              <template slot-scope="scope">
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="delInKinShips(scope.$index)" circle></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item label="目前外村居住">
          <el-button size="small" type="success" @click="addOut">添加</el-button>
          <el-table :data="outKinShips" stripe style="width: 100%">
            <el-table-column prop="kinshipCode" label="亲缘关系" width="180">
              <template slot-scope="scope">
                <el-select :style="scope.row.kinshipCode == '09'?'width:45%':'width:100%'" @input="kinshipCodeValidate(scope.row)" v-model="scope.row.kinshipCode" placeholder="请选择">
                  <el-option v-for="item in relations" :title="item.label" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
                <el-input v-if="scope.row.kinshipCode == '09'" @input="kinshipCodeValidate(scope.row)" :style="scope.row.kinshipCode == '09'?'width:52%':''" v-model="scope.row.kinshipCodeOther"></el-input>
                <el-tooltip v-if="scope.row.kinshipCodeTip" :content="scope.row.kinshipCodeTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="100">
              <template slot-scope="scope">
                <el-input v-model="scope.row.name" style="width:100%;" @input="nameValidate(scope.row)"></el-input>
                <el-tooltip v-if="scope.row.nameTip" :content="scope.row.nameTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="sex" label="性别" width="100">
              <template slot-scope="scope">
                <el-select v-model="scope.row.sex" style="width:100%;">
                  <el-option value="男" label="男"></el-option>
                  <el-option value="女" label="女"></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column prop="birthdate" label="出生日期">
              <template slot-scope="scope">
                <el-date-picker style="width:100%;" @input="birthdateValidate(scope.row)" v-model="scope.row.birthdate" value-format="yyyy-MM-dd" align="right" type="date" placeholder="选择日期">
                </el-date-picker>
                <el-tooltip v-if="scope.row.birthdateTip" :content="scope.row.birthdateTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="idcardNo" label="身份证号">
              <template slot-scope="scope">
                <el-input v-model="scope.row.idcardNo" style="width:100%;" @input="idcardNoTableValidate(scope.row)"></el-input>
                <el-tooltip v-if="scope.row.idcardNoTip" :content="scope.row.idcardNoTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="addr" label="现住址">
              <template slot-scope="scope">
                <el-input v-model="scope.row.addr" style="width:100%;" @input="addrValidate(scope.row)"></el-input>
                <el-tooltip v-if="scope.row.addrTip" :content="scope.row.addrTip"><i class="el-icon-warning" style="color:red;position:absolute;top: 15px;right: 5px;"></i></el-tooltip>
              </template>
            </el-table-column>
            <el-table-column prop="remark" label="备注">
              <template slot-scope="scope">
                <el-input type="textarea" style="width:100%;" v-model="scope.row.remark"></el-input>
              </template>
            </el-table-column>
            <el-table-column prop="remark" label="操作" width="50">
              <template slot-scope="scope">
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="delOutKinShips(scope.$index)" circle></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" :disabled="loading" @click="confirm">提交</el-button>
          <el-button :disabled="loading" @click="cancle">取消</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    var pointManNoValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入此处必要信息'));
      } else {
        if (!/^\d{4,4}$/.test(value)) {
          callback(new Error('请输入4位数字'));
        }
        callback();
      }
    };
    var idCardNoValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入此处必要信息'));
      } else {
        if (!/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(value)) {
          callback(new Error('身份证号不正确'));
        }
        let s = "";
        if (value.length == 18) {
          s = parseInt(value.charAt(16) / 2) * 2 != value.charAt(16) ? "男" : "女"
        } else {
          s = parseInt(value.charAt(14) / 2) * 2 != value.charAt(14) ? "男" : "女"
        }
        if (s != this.form.sex) {
          callback(new Error('身份证号与性别不匹配'));
        }
        callback();
      }
    };
    var mobilePhoneValidate = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入此处必要信息'));
      } else {
        var phone = /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/;
        if (!phone.test(value)) {
          callback(new Error('手机号码不正确，如需输入固话，请在区号后加“-”'));
        }
        callback();
      }
    };
    return {
      loading: false,
      address1Options: [
        { value: "海阳所", label: "海阳所" },
        { value: "南黄", label: "南黄" },
        { value: "乳山口", label: "乳山口" },
        { value: "乳山寨", label: "乳山寨" },
        { value: "午极", label: "午极" },
        { value: "下初", label: "下初" }
      ],
      form: {
        address1: "",
        address2: "",
        inputUser: "",
        inputDate: new Date(),
        pointManNo: "",
        name: "",
        sex: "男",
        healthy: "健在",
        inCurrentCountry: "否",
        birthDate: "",
        currentAddr: "",
        idCardNo: "",
        mobilePhone: "",
        isOtherPointmanRel: "否",
        otherPointmanName: "",
        otherPointmanRel: ""
      },
      rules: {
        address1: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        address2: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        inputUser: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        inputDate: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        pointManNo: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" },
          { validator: pointManNoValidate, trigger: ['blur', 'change'] }
        ],
        name: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        birthDate: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        currentAddr: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        idCardNo: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" },
          { validator: idCardNoValidate, trigger: ['blur', 'change'] }
        ],
        mobilePhone: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" },
          { validator: mobilePhoneValidate, trigger: ['blur', 'change'] }
        ],
        otherPointmanName: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ],
        otherPointmanRel: [
          { required: true, message: "请输入此处必要信息", trigger: "blur" }
        ]
      },
      relations: [
        { label: "①父亲", value: "01" },
        { label: "②母亲", value: "02" },
        { label: "③夫妻", value: "03" },
        { label: "④未婚子女", value: "04" },
        { label: "⑤已婚子女", value: "05" },
        { label: "⑥未婚孙儿(女)/外孙儿(女)", value: "06" },
        { label: "⑦已婚孙儿(女)/外孙儿(女)", value: "07" },
        { label: "⑧兄弟姐妹", value: "08" },
        { label: "⑨其他", value: "09" }
      ],
      inKinShips: [],
      outKinShips: []
    };
  },
  mounted() {
    if (!sessionStorage.getItem("userName")) {
      this.$router.push({ path: "/" });
    }
  },
  methods: {
    addrChange() {
      this.form.currentAddr = "乳山市" + this.form.address1 + "乡镇" + this.form.address2 + "村（居委会）";
    },
    nameValidate(row) {
      let value = row.name;
      if (value === '') {
        row.nameTip = '请输入此处必要信息';
        return false;
      }
      row.nameTip = '';
      return true;
    },
    birthdateValidate(row) {
      let value = row.birthdate;
      if (value === '') {
        row.birthdateTip = '请输入此处必要信息';
        return false;
      }
      row.birthdateTip = '';
      return true;
    },
    addrValidate(row) {
      let value = row.addr;
      if (value === '') {
        row.addrTip = '请输入此处必要信息';
        return false;
      }
      row.addrTip = '';
      return true;
    },
    kinshipCodeValidate(row) {
      let value = row.kinshipCode;
      if (value === '' || (value == "09" && !row.kinshipCodeOther)) {
        row.kinshipCodeTip = '请输入此处必要信息';
        return false;
      }
      row.kinshipCodeTip = '';
      return true;
    },
    idcardNoTableValidate(row) {
      let value = row.idcardNo;
      let sex = row.sex;
      if (value === '') {
        row.idcardNoTip = '请输入此处必要信息';
        return false;
      } else {
        if (!/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(value)) {
          row.idcardNoTip = '身份证号不正确';
          return false;
        }
        let s = "";
        if (value.length == 18) {
          s = parseInt(value.charAt(16) / 2) * 2 != value.charAt(16) ? "男" : "女"
        } else {
          s = parseInt(value.charAt(14) / 2) * 2 != value.charAt(14) ? "男" : "女"
        }
        if (s != sex) {
          row.idcardNoTip = '身份证号与性别不匹配';
          return false;
        }
        row.idcardNoTip = '';
        return true;
      }
    },
    confirm() {
      let self = this;
      self.$refs["form"].validate(valid => {
        if (valid) {
          self.form.addr = "乳山市" + self.form.address1 + "乡镇" + self.form.address2 + "村（居委会）";
          if (self.inKinShips.length == 0) {
            self.$message.warning("请添加目前本村居住数据！");
            return false;
          }
          if (self.outKinShips.length == 0) {
            self.$message.warning("请添加目前外村居住数据！");
            return false;
          }
          let flag1 = true;
          let flag2 = true;
          self.outKinShips.forEach((item) => {
            flag1 = self.kinshipCodeValidate(item) && self.nameValidate(item) && self.birthdateValidate(item) && self.idcardNoTableValidate(item) && self.addrValidate(item);
          });
          self.inKinShips.forEach(item => {
            flag2 = self.kinshipCodeValidate(item) && self.nameValidate(item) && self.birthdateValidate(item) && self.idcardNoTableValidate(item);
          });
          if (!flag2) {
            console.log(JSON.stringify(self.inKinShips))
            self.$message.warning("目前本村居住数据错误，请检查！");
            return false;
          }
          if (!flag1) {
            console.log(JSON.stringify(self.outKinShips))
            self.$message.warning("目前外村居住数据错误，请检查！");
            return false;
          }
          self.form.outKinShips = self.outKinShips;
          self.form.inKinShips = self.inKinShips;
          console.log(JSON.stringify(self.form));
          self.loading = true;
          self.axios({
            method: "post",
            url: "http://" + self.serviceAddr + ":8050/qs/api/v1/addpm",
            data: JSON.stringify(self.form),
            headers: {
              "Content-Type": "application/json"
            }
          }).then(response => {
            if (response.data.code == 0) {
              self.$message.success("保存成功！");
              self.$router.push({ path: "/list" });
            } else {
              self.$message.error(response.data.msg);
            }
            self.loading = false;
          }).catch(error => {
            self.loading = false;
            self.$message.error(error);
          });
        } else {
          return false;
        }
      });
    },
    addIn() {
      this.inKinShips.push({
        kinshipCode: "",
        kinshipCodeTip: "",
        name: "",
        nameTip: "",
        sex: "男",
        birthdate: "",
        birthdateTip: "",
        idcardNo: "",
        idcardNoTip: "",
        remark: ""
      });
    },
    addOut() {
      this.outKinShips.push({
        kinshipCode: "",
        kinshipCodeTip: "",
        name: "",
        nameTip: "",
        sex: "男",
        birthdate: "",
        birthdateTip: "",
        addr: "",
        addrTip: "",
        idcardNo: "",
        idcardNoTip: "",
        remark: ""
      });
    },
    delInKinShips(index) {
      this.inKinShips.splice(index, 1);
    },
    delOutKinShips(index) {
      this.outKinShips.splice(index, 1);
    },
    cancle() {
      this.$router.push({ path: "/list" });
    }
  }
};
</script>
<style scoped></style>



